import { useState, useMemo } from 'react';
import './App.css';
import SignInPage from "./pages/auth/SignInPage";
import MenuAppBar from "./components/Header"; //  Header
import FinancialReport from "./components/FinancialReport";
import { createTheme, ThemeProvider } from '@mui/material/styles';

//   
const initialUserState = {
  me: { name: '', surname: '', phone: '', email: '' },
  restaurant: { name: '', address: '' },
  finances: { balance: null, salary: null, tips: null, salesPercentage: null },
  workSchedule: { shifts: [] }
};

function App() {
  const [isAuth, setIsAuth] = useState(false); //    
  const [user, setUser] = useState(initialUserState);
  const [isDarkMode, setIsDarkMode] = useState(false);

  const theme = useMemo(() => createTheme({
    palette: {
      mode: isDarkMode ? 'dark' : 'light',
    },
  }), [isDarkMode]);

  //  
  const handleIsAuth = () => {
    setIsAuth(true);
    updateUserProfile({ me: { name: '', surname: '' } });
    updateFinances({ salary: 2000, tips: 500 });
  };

  //  
  const handleLogout = () => {
    setIsAuth(false);
    setUser(initialUserState); //  
  };

  //   
  const toggleTheme = () => {
    setIsDarkMode(prevMode => !prevMode);
  };

  //   
  const updateUserProfile = (newData) => {
    setUser(prevState => ({
      ...prevState,
      me: {
        ...prevState.me,
        ...newData.me
      }
    }));
  };

  //   
  const updateFinances = (newFinances) => {
    setUser(prevState => ({
      ...prevState,
      finances: {
        ...prevState.finances,
        ...newFinances
      }
    }));
  };

  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <MenuAppBar 
          auth={isAuth} //   
          handleLogout={handleLogout} //   
          user={user} //   
          toggleTheme={toggleTheme} //    
          isDarkMode={isDarkMode} //   
        />

        {isAuth ? (
          <main className="App-main app-container">
            <FinancialReport user={user} />
            <button className="main-add-working-shift"> </button>
          </main>
        ) : (
          <SignInPage handleIsAuth={handleIsAuth} />
        )}

        <footer className="App-footer app-container">
          <p></p>
        </footer>
      </div>
    </ThemeProvider>
  );
}

export default App;
